*,
::after,
::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");

@font-face {
  font-family: "Proxima Nova";
  src: url(/public/fonts/Proxima\ Nova\ Font.otf);
  font-style: normal;
  font-display: fallback;
}

@font-face {
  font-family: "Bebas Neue Pro";
  src: url(/public/fonts/BebasNeue\ Bold.ttf);
  font-style: normal;
  font-display: fallback;
}

:root {
  /* Primary Color  */
  --red: #e1011a;
  --white: #ffffff;
  --violet: #2b2840;
  --violetOpacity: #2b284059;
  --violet-dark: #201e31;
  /* Secodary Color */
  --background: #f1f1f1;
  --gray1: #e5e5e5;
  --gray2: #4f4f4f;
  --gray3: #828282;
  --gray4: #f1f1f1;
  --gray5: #a3a3a9;
  --gray6: #f4f4f4;
  --navigate__left: #bfbfc6;
  --dark--red: #c40720;
  --green: #14b685;
  --border__green: #8bdfc7;
  --background__green: #eaf9f6;
  --hover__select: rgb(222, 235, 255);

  /* SIZES */
  --cerntered-content: 1170px;
}
