.page-item-page {
  color: var(--violet);
  cursor: pointer;
  list-style: none;
  font-size: 19px;
  line-height: 32px;
}
.submit-btn {
  margin-top: 5px;
  margin-bottom: 5px;
  background-color: var(--violet);
  border-radius: 5px;
  padding: 10px;
  color: white;
}
.submit-btn-disabled {
  margin-top: 5px;
  margin-bottom: 5px;
  background-color: var(--gray3);
  border-radius: 5px;
  padding: 10px;
  color: white;
  cursor: auto;
}
.p-3 {
  background-color: #2b28401c;
  margin: 10px;
  padding: 5px;
  border-radius: 10px;
}
.sb-avatar__image {
  border-radius: 50%;
  width: 60px !important;
  height: 60px !important;
}
.sb-avatar--src {
  width: 60px !important;
  height: 60px !important;
}

._3f-mT {
  cursor: pointer;
  background-color: var(--red);
  color: white;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 10px;
}

.pagination {
  display: flex;
  justify-content: space-between;
}
.page-item {
  color: white;
  background-color: var(--red);
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
}
.selected {
  color: var(--red);
  background-color: transparent;
}
