} */
.mb-0 {
  color: var(--violet);
  font-size: 15px;
}
.mb-1 {
  color: var(--violet);
}
._1FtUZ {
  width: -webkit-fill-available;
  margin-left: 20px;
  margin-right: 20px;
}
.h1 {
  font-family: "Bebas Neue", cursive;
  font-style: normal;
  font-weight: 400;
  font-size: 54px;
  line-height: 32px;
}

.h2 {
  font-family: "Bebas Neue";
  font-size: 40px;
  line-height: 39px;
  color: var(--violet);
  text-transform: uppercase;
  font-weight: 400;
}

.h3 {
  font-size: 38px;
  line-height: 42px;
  font-weight: 700;
}

.h4 {
  font-size: 27px;
  line-height: 32px;
  font-weight: 400;
  color: var(--violet);
  text-transform: uppercase;
  font-family: "Bebas Neue Pro";
  white-space: nowrap;
}

.h5 {
  color: var(--violet);
  text-transform: uppercase;
  font-weight: 400;
  font-size: 40px;
  line-height: 39px;
  font-family: "Bebas Neue Pro";
  white-space: nowrap;
}

.liClass {
  font-family: "Bebas Neue", cursive;
  white-space: nowrap;
  color: var(--violet);
  list-style: none;
  font-size: 19px;
  line-height: 32px;
  cursor: pointer;
}

.ant-tabs-tab {
  font-family: "Bebas Neue", cursive;
  white-space: nowrap;
  color: var(--violet);
  list-style: none;
  font-size: 19px !important;
  line-height: 32px;
  cursor: pointer;
}
.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: var(--red) !important;
  text-shadow: 0 0 0.25px var(--red) !important;
}
.ant-tabs-tab-btn:focus,
.ant-tabs-tab-remove:focus,
.ant-tabs-tab-btn:active,
.ant-tabs-tab-remove:active {
  color: var(--red) !important;
}
.ant-tabs-tab-remove:hover {
  color: rgba(0, 0, 0, 0.85) !important;
}

.ant-tabs-tab:hover {
  color: var(--red) !important;
}
.ant-tabs-ink-bar {
  border-bottom: 2px solid var(--red) !important;
}
p {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}

a {
  text-decoration: none;
  white-space: nowrap;
}

input {
  color: var(--gray3);
  border: 0;
  background-color: transparent;
}

input:focus {
  outline: 0;
}

select:focus {
  outline: 0;
}

html,
body {
  font-family: "Proxima Nova Pro";
  font-weight: 400;
}
button {
  border: 0;
  cursor: pointer;
}
.lazy-load-image-background.blur.lazy-load-image-loaded {
  display: "contents" !important;
}
.lazy-load-image-background.blur.lazy-load-image-loaded > img {
  object-fit: cover !important;
  max-width: 100%;
  max-height: 100%;
}

video {
  width: 900 !important;
  height: 350px !important;
}
